<template>
	<div class="content-wrapper">
		<img class="banner" :src="pageState.banner" alt="">
		<div class="content-box">
			<!-- 面包屑导航 -->
			<div class="breadcrumb-box">
				<a-breadcrumb>
					<a-breadcrumb-item href="">
						<home-outlined />
						<span>首页</span>
					</a-breadcrumb-item>
					<a-breadcrumb-item href="">
						<span>{{pageState.name}}</span>
					</a-breadcrumb-item>
					<a-breadcrumb-item>{{pageState.title}}</a-breadcrumb-item>
				</a-breadcrumb>
			</div>
			<!-- 分类和内容 -->
			<div class="main-box flex-row">
				<!-- 分类 -->
				<div class="type-box" v-if="pageState.isShowMenu">
					<div class="type-title flex-row"><span>{{pageState.name}}</span><span>About us</span></div>
					<div 
						v-for="(item, index) of pageState.typeList" 
						:key="index" 
						:class="pageState.active == index ? 'type-item active' : 'type-item'"
						@click="onTypeClick(index, item)"
					>
						<img :src="pageState.active == index ? require('../../assets/type-item-selected.png') : require('../../assets/type-item.png')" alt="">
						{{item.menuName}}
					</div>
				</div>
				<div class="main-content">
					<router-view></router-view>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { defineComponent, ref, watchEffect, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { HomeOutlined } from '@ant-design/icons-vue'
import { selectMenuList } from '@/api/api.js'
import eventBus from '@/utils/eventBus.js'
export default defineComponent({
  components: {
    HomeOutlined
  },
	setup () {
		const router = useRouter(), route = useRoute()
		const pageState = ref({
						active: 0,
						type: 0,
						typeList: [],
						name: '',
						title: '',
						isShowMenu: true
					})
		onMounted(() => {
		})
		eventBus.$on('content', () => {
			if (localStorage.getItem('contentData')) {
				let nav = JSON.parse(localStorage.getItem('navList'))
				let navFilter = nav[JSON.parse(localStorage.getItem('contentData')).type]
				pageState.value.banner = JSON.parse(navFilter.urls).length ? JSON.parse(navFilter.urls)[0] : require('@/assets/default-img.png')
				pageState.value.type = JSON.parse(localStorage.getItem('contentData')).type
				pageState.value.name = JSON.parse(localStorage.getItem('contentData')).name
				pageState.value.isShowMenu = JSON.parse(localStorage.getItem('contentData')).type == 6 ? false : true
				pageState.value.active = localStorage.getItem('type')
				selectMenuList(
					{	
						menuId: JSON.parse(localStorage.getItem('contentData')).id,
						language: 'en'
					}
				).then((res) => {
					if (res.code === 200) {
						
						pageState.value.typeList = res.data
						pageState.value.title = pageState.value.typeList[localStorage.getItem('type')].menuName
						loadInitData(localStorage.getItem('type'))
					}
				})
			}
		})
		// header nav切换时监听type的值
		watchEffect(() => {
			if (route.query.type && route.path == '/content') {
				pageState.value.type = route.query.type
				pageState.value.name = route.query.name
				pageState.value.isShowMenu = route.query.type == 6 ? false : true
				let urls = JSON.parse(localStorage.getItem('navList')).filter(item => { return item.menuId == route.query.id})[0].urls
				pageState.value.banner = JSON.parse(urls).length ? JSON.parse(urls)[0] : require('@/assets/default-img.png')
				let contentData = {
					type: route.query.type,
					id: route.query.id,
					name: route.query.name,
				}
				localStorage.setItem('contentData', JSON.stringify(contentData))
				selectMenuList({menuId: route.query.id}).then((res) => {
					if (res.code === 200) {
						pageState.value.typeList = res.data
						pageState.value.title = pageState.value.typeList[0].menuName
						loadInitData(0)
					}
				})
			}
		})
		const loadInitData = (index) => {
			if (pageState.value.typeList) {
				let init = pageState.value.typeList[index]
				if (init.menuType === 'article') {
					router.push({ path: '/detail', query:{ menuId: init.menuId } })
				} else if (init.menuType === 'list' ) {
					if (route.query.type == 2 || route.query.type == 3) {
						router.push({ path: '/list', query:{ menuId: init.menuId } })
					} else if (route.query.type == 5){
						router.push({ path: '/news', query:{ menuId: init.menuId } })
					}
				}
			}
		}
		const onTypeClick = (index, item) => {
			pageState.value.active = index
			pageState.value.title = pageState.value.typeList[index].menuName
			localStorage.setItem('type', index)
			let contentData = JSON.parse(localStorage.getItem('contentData'))
			if (contentData) {
				if (item.menuType === 'article') {
					router.push({ path: '/detail', query:{ menuId: item.menuId } })
				} else if (item.menuType === 'list') {
					if (contentData.type == 2 || contentData.type == 3) {
						router.push({ path: '/list', query:{ menuId: item.menuId } })
					} else if (contentData.type == 5){
						router.push({ path: '/news', query:{ menuId: item.menuId } })
					}
				}
			} else if (route.query.type) {
				if (item.menuType === 'article') {
					router.push({ path: '/detail', query:{ menuId: item.menuId } })
				} else if (item.menuType === 'list') {
					if (route.query.type == 2 || route.query.type == 3) {
						router.push({ path: '/list', query:{ menuId: item.menuId } })
					} else if (route.query.type == 5){
						router.push({ path: '/news', query:{ menuId: item.menuId } })
					}
				}
			}
			
		}
		function goPage (url) {
			router.push({
				path: url, 
				query: {
					type: pageState.value.type,
					id: route.query.id,
					name: route.query.name,
				}
			})
		}
		return {
			pageState,
			onTypeClick
		}
	}
})

</script>

